import React, { useContext } from "react";
import "./Header.scss";
import Vector1 from "../../img/Vector1.png";
import Vector2 from "../../img/Vector2.png";
import boy from "../../img/boy.png";
import glassesimoji from "../../img/glassesimoji.png";
import thumbup from "../../img/thumbup.png";
import crown from "../../img/crown.png";
//import FloatinDiv from "../FloatingDiv/FloatingDiv";
import Github from "../../img/github.png";
import LinkedIn from "../../img/linkedin.png";
import Instagram from "../../img/instagram.png";
//import { themeContext } from "../../Context";
import { motion } from "framer-motion";
//import { Link } from "react-scroll";
const Header = () => {
  // Transition
  const transition = { duration: 2, type: "spring" };

  // context
 // const theme = useContext(themeContext);
 // const darkMode = theme.state.darkMode;

  return (
    <div className="Intro" id="Intro">
      {/* left name side */}
      <div className="i-left">
        <div className="i-name">
          {/* yahan change hy darkmode ka */}
          <span /*</div></div>style={{ color: darkMode ? "white" : "" }}*/>Hy! This is</span>
          <span>Waleed</span>
          <span>
            Frontend Developer with high level of experience in designing
            and development, producting the Quality of work
          </span>
        </div>
      {/*  <Link to="contact" smooth={true} spy={true}>  </Link> */}  
        <div className="button-left"> <button className="button i-button">Hire me</button></div> 
        
        {/* social icons */}
        <div className="i-icons">
          <img src={Github} href="https://github.com/chwalleeed" alt="" />
          <img src={LinkedIn} href="https://www.linkedin.com/in/waleed-nasir-98772b195/" alt="" />
          <img src={Instagram} href="https://www.instagram.com/ch_waleeed/" alt="" />
        </div>
      </div>
      {/* right image side */}
      <div className="i-right">
        <img src={Vector1} alt="" />
        <img src={Vector2} alt="" />
        <img className="boy" src={boy} alt="" />
        {/* animation */}
        <motion.img
          initial={{ left: "-36%" }}
          whileInView={{ left: "-24%" }}
          transition={transition}
          src={glassesimoji}
          alt=""
        />

        <motion.div
          initial={{ top: "-4%", left: "74%" }}
          whileInView={{ left: "68%" }}
          transition={transition}
          className="floating-div"
        >
        <div className="tag-cmp ">
          <p className="p-text">Web Developer,</p>
          <p className="p-text">Freelancer,</p>
          <p className="p-text">Graphic Designer</p>
        </div>
        </motion.div>

        {/* animation */}
        <motion.div
          initial={{ left: "9rem", top: "18rem" }}
          whileInView={{ left: "0rem" }}
          transition={transition}
          className="floating-div"
        >
          {/* floatinDiv mein change hy dark mode ka */}
      {/**     <FloatinDiv img={thumbup} text1="Best Design" text2="Award" />  */}
        </motion.div>

        
        </div>
      </div>
    
  );
};

export default Header;